import React from 'react';
import s from './PagesNavigation.module.css';
import { useState } from 'react';
import cn from 'classnames';

const PagesNavigation = ({totalItemsCount, pageSize, pageNumber, onPageChange, portionSize=10}) => {
   
    let pagesCount = Math.ceil(totalItemsCount/pageSize);
    let pages = [];
    for (let i=1; i<=pagesCount ; i++) {
        pages.push(i);
    }

    let portionCount = Math.ceil(pagesCount/portionSize)
    let [portionNumber, setPortionNumber] = useState(1)
    let leftPortionPageNumber = (portionNumber-1) * portionSize + 1
    let rightPortionPageNumber = portionNumber * portionSize


    
    // let curPage=pageNumber;
    // let curPageFirst=((curPage-5)<0) ? 0 : curPage-5;
    // let curPageLast=curPage+4;
    // let slicedPages=pages.slice(curPageFirst, curPageLast)
    return (
        <div className = {s.paginator}>
        { leftPortionPageNumber > 1 && 
            <button onClick={ () => {setPortionNumber(portionNumber - 1)}}>PREV</button>}
            {pages.filter(p => p>=leftPortionPageNumber && p<=rightPortionPageNumber)
                .map( p => {
                    return <span className={cn ({
                        [s.selectedPage]: pageNumber === p
                    }, s.pageNumber)} key={p} onClick={ (e) => {
                        onPageChange(p)
                    }}>{p}</span>
        })}

        { portionCount > portionNumber && 
            <button onClick={ () => {setPortionNumber(portionNumber+1)}}>NEXT</button>}
        

        </div>
    )
        // <div>
        //     {slicedPages.map ( p => {
        //         return <span key={p} onClick={() => { onPageChange(p)}} className={p === pageNumber ? s.selectedPage : ''}> {p} </span>
        //     })}
        // </div>)
}
export default PagesNavigation;